.SimulationViewport-linkLines {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  stroke: #d1c7c7;
  stroke-width: 2px;
  fill: none; }

.SimulationViewport-linkLines marker#arrow {
  stroke-width: 0;
  fill: #d1c7c7; }

.SimulationViewport-FPSView {
  position: absolute;
  z-index: 1000;
  left: 0;
  bottom: 0;
  background-color: darkblue;
  opacity: 0.8;
  color: white;
  font-size: 1em;
  padding: 0.5em; }



